exports.push([module.id, "\n\n\n\n\n/*@import \"../../styles/style.css\";*/\n", "", {"version":3,"sources":["/Users/ty/wbPro/jobAll/portal-v2/src/components/basic/src/components/basic/Login.vue"],"names":[],"mappings":";;;;;AAKA,qCAAA","file":"Login.vue","sourcesContent":["/*********************************************************************\n*                                                            *\n* Created by tr on 2017/6/27.                                        *\n*********************************************************************/\n<style scoped>\n    /*@import \"../../styles/style.css\";*/\n</style>\n<template>\n    <div id=\"Login\" class=\"Login\">\n        <div class=\"logo-box\">\n            <img src=\"\" alt=\"logo\" style=\"visibility: hidden\"/>\n        </div>\n        <!-- <div class=\"client-box\" style=\"position:absolute;top:15px;right:10px;padding:2px;\">\n             <a  href=\"/publish/portal-setup-1.1.6.exe\">windows</a>\n         </div>-->\n        <!---->\n        <div class=\"login-wrapper\">\n            <div class=\"header\">\n                <h3></h3>\n                <h4>UNIFIED SERVICE PLATFORM</h4>\n                <img src=\"../../images/login-avatar.png\" alt=\"avatar\"/>\n            </div>\n            <form>\n                <div class=\"control-group\">\n                    <label><img src=\"../../images/account.png\" alt=\"account\"/><span></span></label>\n                    <input type=\"text\" class=\"form-control\"\n                           placeholder=\"\" required=\"true\" v-model=\"userName\">\n                </div>\n                <div class=\"control-group\">\n                    <label><img src=\"../../images/pwd.png\" alt=\"account\"/><span></span></label>\n                    <input class=\"form-control\"\n                           type=\"password\" style=\"\"\n                           placeholder=\" \" required=\"true\"  v-model=\"password\">\n                </div>\n                <div class=\"checkbox\" style=\"text-align: left;color: #ccc;width: 85%;\">\n                    <input type=\"checkbox\" class=\"checkbox\"> \n                </div>\n                <div class=\"msg-group\" v-bind:class=\"{'show':isShow}\">\n                    <p id=\"msgTxt\">{{loginDataValue}}</p>\n                </div>\n                <template v-if=\"!isShow\">\n                    <button type=\"submit\" class=\"btn btn-primary\" @click=\"loginEle\">\n                        <span class=\"text\"></span>\n                    </button>\n                </template>\n                <template v-else>\n                    <button type=\"submit\" class=\"btn btn-primary\">\n                        <img src=\"../../images/loading_white.gif\" width=\"20\">\n                    </button>\n                </template>\n            </form>\n        </div>\n        <!--<div class=\"footer\">-->\n        <!--<p>Copyright  2017 <a href=\"javascript:\"></a> </p>-->\n        <!--</div>-->\n\n        <div class=\"footer-software-dept\">\n            <div class=\"img\"></div>\n        </div>\n    </div>\n</template>\n<script>\n    import { mapGetters } from \"vuex\"\n    export default{\n        /**\n         * @returns {{\n         * password: string ,\n         * userName: string ,\n         * isShow: boolean ,\n         * loginDataValue: string \n         * }}\n         */\n        data(){\n            return {\n                password:'123456',\n                userName:'admin',\n                isShow:false,\n                loginDataValue : ''\n            }\n        },\n        computed: mapGetters({\n            /**/\n            loginData: \"loginData\"\n        }),\n        watch:{\n            /**\n             * \n             * @param val \n             */\n            loginData(val){\n                const that = this;\n\n                that.loginDataValue = val.msg;\n                that.isShow = true;\n                /*2s*/\n                if (val) {\n                    setTimeout(function () {\n                        that.loginDataValue = \"\";\n                        that.isShow = false;\n                    }, 2000);\n                }\n            }\n        },\n        methods : {\n            /**\n             * \n             */\n            loginEle : function () {\n                const that = this;\n//                that.$store.dispatch('loadingShow');\n                let password = that.password,\n                    username = that.userName;\n                if(password && username) {\n                    let reqData = {\n                        password: password,\n                        username: username\n                    };\n                    that.$store.dispatch('login', {reqData});\n                }\n            }\n        },\n\n        created(){\n            console.log('login.vue created');\n            debugger;\n        }\n    }\n</script>\n"],"sourceRoot":""}]);

